<template>
	<nav class="mr-md-4">
		<div class="nav-logo m-b-md">
			<button class="d-md-none navbar-toggle" data-toggle="collapse" data-target="#nav-toggleable-md"></button>
			<p class="nav-link m-0" href="/">
				<img src="img/packr-icon.svg">
				<span class="ml-2">FESTIVAL Pack'd</span>
			</p>
		</div>
		
		<div class="collapse d-md-block" id="nav-toggleable-md">
			<ul class="nav nav-pills nav-stacked flex-column mb-4">
				<li class="nav-header">Links</li>
				<li class="nav-item mb-2">
					<router-link to="/" tag="p" class="nav-link" exact>
						<span class="icon icon-home" />
						Dashboard
					</router-link>
				</li>
				<li class="nav-item mb-2" v-if="isDeveloper || isModerator || isAdministrator">
					<router-link to="/packages" tag="p" class="nav-link">
						<span class="icon icon-dropbox" />
						Packages
					</router-link>
				</li>
				<li class="nav-item mb-2">
					<router-link to="/reviews" tag="p" class="nav-link disabled" :class="{'active': isActiveLink('/reviews')}">
						<span class="icon icon-chat" />
						Reviews
					</router-link>
				</li>
				<li class="nav-item mb-2">
					<router-link to="/devices" tag="p" class="nav-link disabled" :class="{'active': isActiveLink('/devices')}">
						<span class="icon icon-tablet-mobile-combo" />
						Devices
					</router-link>
				</li>
				<li class="nav-item mb-2" v-if="isModerator || isAdministrator">
					<router-link to="/users" tag="p" class="nav-link disabled" :class="{'active': isActiveLink('/users')}">
						<span class="icon icon-users" />
						Users
					</router-link>
				</li>
				<li class="nav-item mb-2" v-if="isModerator || isAdministrator">
					<router-link to="/statistics" tag="p" class="nav-link disabled" :class="{'active': isActiveLink('/statistics')}">
						<span class="icon icon-line-graph" />
						Statistics
					</router-link>
				</li>
				<li class="nav-header">Actions</li>
				<li class="nav-item">
					<p class="nav-link" @click="signOut()">
						<span class="icon icon-log-out" />
						Sign Out
					</p>
				</li>
			</ul>
			
			<hr class="d-md-none">
		</div>
	</nav>
</template>

<style lang="less">
.router-link-exact-actve {
	&:extend(.active);	
}
</style>

<script>
import UserRole from "../../../server/UserRole"

export default {
	name: "navComp",
	data() {
		return {
			isAdministrator: false,
			isModerator: false,
			isDeveloper: false
		}
	},
	created() {
		let role = this.$store.getters.userRole;
		
		this.isAdministrator = (role & UserRole.ADMINISTRATOR) == UserRole.ADMINISTRATOR;
		this.isModerator = (role & UserRole.MODERATOR) == UserRole.MODERATOR;
		this.isDeveloper = (role & UserRole.DEVELOPER) == UserRole.DEVELOPER;
	},
	methods: {
		isActiveLink(item) {
			let match = window.location.hash.match(`#${item}`);
			return match && match.length
		},
		signOut() {
			const self = this;
			bootbox.confirm({
				title: "Sign out?",
				message: "Do you really want to sign out? All unsaved progress will be lost.",
				buttons: {
					confirm: {
						label: 'Sign out',
						className: 'btn-danger'
					},
					cancel: {
						label: 'Cancel',
						className: 'btn-secondary'
					}
				},
				callback: async (result) => {
					if (result) {
						self.$cookies.remove("authToken");
						self.$router.replace("/login");
					}
				}
			});
		}
	}
}
</script>