<template>
	<div class="switch">
		<div class="switcher" :class="{'checked': value}" @click="toggle()">
			<input type="checkbox" :name="checkboxName" :id="checkboxName" v-bind:checked="checked" v-on:input="updateValue($event.target.checked)">
			<div class="switch-inner"></div>
		</div>
	</div>
</template>

<script>
export default {
	name: "SwitchCheckbox",
	props: ["checkboxName", "value"],
	data() {
		return {
			checked: this.$props.value
		}
	},
	updated() {
		this.checked = this.$props.value
	},
	methods: {
		toggle: function (value) {
			this.checked = !this.checked;
			this.$emit('input', this.checked);
		}
	},
	
}
</script>