<template>
	<div class="container">
		<div class="row">
			<div class="col-md-3">
				<Nav />
			</div>
			<div class="col-md-9">
				<div v-if="!packageData || !packageData.status">
					<div class="dashhead mb-3">
						<div class="dashhead-titles">
							<h6 class="dashhead-subtitle">Packages</h6>
							<h2 class="dashhead-title">Edit Package</h2>
						</div>
					</div>

					<div class="row">
						<div class="col-6 text-left">
							<button class="btn btn-danger" :disabled="!$v.$anyDirty || isDoingWork" @click="cancel()">
								<span class="icon icon-squared-cross" />
								Cancel
							</button>
						</div>
						<div class="col-6 row p-0 justify-content-end text-right">
							<div class="col p-0 pr-3" v-if="isDoingWork">
								<div class="loading-indicator" />
							</div>
							<div class="col-auto p-0">
								<button class="btn btn-primary" @click="savePackage()" :disabled="$v.$invalid || !$v.$anyDirty || isDoingWork">
									<span class="icon icon-save" />	
									Save
								</button>
							</div>
						</div>
					</div>
				</div>
				
				<div v-if="packageData && packageData.status == 404">
					<vue-headful title="Package not found  FESTIVAL Pack'd" />
					<h4>Package not found</h4>
					<p>Could not retrieve info for package "{{ $route.params.packageId }}"</p>
				</div>
				
				<div v-if="packageData && !packageData.status">
					<vue-headful :title="`${packageData.name}  FESTIVAL Pack'd`" />
					<form name="editPackageForm" novalidate>
						<h4 class="my-4">General Info</h4>
						<div class="row">
							<div class="col-md-6 form-group">
								<label>Package Name</label>
								<input type="text" class="form-control" placeholder="Example Package" v-model="packageData.name" maxlength="50" autocomplete="off" @input="$v.$touch()" :disabled="isDoingWork">
								<p class="text-muted text-right m-b-0">{{50 - packageData.name.length}}</p>
							</div>
							<div class="col-md-6 form-group">
								<label>Bundle Identifier</label>
								<input type="text" class="form-control" placeholder="com.example.package" v-model="packageData.identifier" maxlength="50" autocomplete="off" @input="$v.$touch()" disabled>
								<p class="text-muted text-right m-b-0">{{50 - packageData.identifier.length}}</p>
							</div>
						</div>
						<div class="row">
							<div class="col-md-6 form-group">
								<label for="login-password">Short Description</label>
								<textarea class="form-control" v-model="packageData.shortDescription" maxlength="255" autocomplete="off" @input="$v.$touch()" style="height: 100px" :disabled="isDoingWork" />
								<p class="text-muted text-right m-b-0">{{255 - packageData.shortDescription.length}}</p>

								<label>Description</label>
								<textarea name="detailedDescription" class="form-control" v-model="packageData.detailedDescription" maxlength="4000" autocomplete="off" @input="$v.$touch()" style="height: 250px" :disabled="isDoingWork"/>
								
								<div class="row">
									<div class="col-9">
										<p class="text-muted text-left m-b-0">Descriptions support HTML</p>
									</div>
									
									<div class="col-3">
										<p class="text-muted text-right m-b-0">{{4000 - packageData.detailedDescription.length}}</p>
									</div>
								</div>
							</div>
							<div class="col-md-6 form-group">
								<label>Support URL</label>
								<input type="text" class="form-control mb-4" v-model="packageData.bugsReportURL" placeholder="http://example.com (optional)" autocomplete="off" @input="$v.$touch()" :disabled="isDoingWork">

								<div class="row">
									<div class="col-6">
										<label>Minimum OS</label>
										<input type="text" class="form-control" placeholder="11.0" v-model="packageData.minOSVersion" maxlength="30" autocomplete="off" @input="$v.$touch()" :disabled="isDoingWork">
									</div>
									<div class="col-6">
										<label>Maximum OS</label>
										<input type="text" class="form-control" placeholder="11.3.1" v-model="packageData.maxOSVersion" maxlength="30" @input="$v.$touch()" autocomplete="off" :disabled="isDoingWork">
									</div>
								</div>
							</div>
						</div>
						<div class="row justify-content-between">
							<div class="col-6 col-md-3 mt-4 mt-md-0">
								<h4 class="mb-4">Store Icon</h4>
								<PackageIconSelector :class="{'disabled': isDoingWork}" v-model="selectedPackageIcon" :packageIcon="packageData.icon" @input="iconChanged" v-if="packageData.icon !== undefined" />
							</div>
							<div class="list-group col-md-6 mt-4 mt-md-0 m-b px-3">
								<h4 class="mb-4">Options</h4>
								<div class="list-group-item d-flex justify-content-between align-items-center">
									<div class="col-auto p-0">
										<p class="m-0">Package is visible</p>
									</div>
									<div class="col-auto p-0">
										<SwitchCheckbox :class="{'disabled': isDoingWork}" checkboxName="visible" v-model="packageData.visible" @input="$v.$touch()" />
									</div>
								</div>
							</div>
						</div>
						
						<h4 class="my-4">Screenshots</h4>
						<ScreenshotSelector class="d-none d-md-block" :class="{'working': isDoingWork}" v-if="screenshots" v-model="screenshots" :deletedScreenshots="deletedScreenshots" :packageObj="packageData" @touched="$v.$touch()" />
						<p class="d-md-none">To manage screenshots, open Pack'd on your desktop computer.</p>
					</form>
					<div class="row my-4">
						<div class="col-8">
							<h4 class="">Versions</h4>
						</div>
						<div class="col-4 text-right">
							<router-link tag="button" class="btn btn-primary" :to="`/packages/${packageData.identifier}/versions/add`">
								<span class="icon icon-new-message" />
								Add
							</router-link>
						</div>
					</div>
					<div class="table-responsive">
						<table class="tablesort" data-sort="table">
							<thead>
								<tr>
									<th class="header">Version</th>
									<th class="header">Visible</th>
									<th class="header sorter-mmddyy d-none d-md-table-cell">Created</th>
									<th class="tablesorter-noSort text-right">Actions</th>
								</tr>
							</thead>
							<tbody>
								<router-link tag="tr" :to="`/packages/${$route.params.packageId}/versions/${versionObj.version}`" :key="versionObj.id" v-for="versionObj in versionData">
									<td>{{ versionObj.version }}</td>
									<td>
										<SwitchCheckbox v-model="versionObj.visible" />
									</td>
									<td class="d-none d-md-table-cell">{{ versionObj.createdAt | date }}</td>
									<td class="text-right">
										<button class="btn btn-outline-danger" @click.prevent="deleteVersion(versionObj.id)" :disabled="versionData.length <= 1">
											<span class="icon icon-trash" />
										</button>
									</td>
								</router-link>
								<tr v-if="!versionData.length">
									<td>This package doesnt have any versions.</td>
								</tr>
							</tbody>
						</table>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<style lang="less" scoped>
.icon-preview {
	box-shadow: inset 0 0 0 1px #434857;
}

.table-responsive {
	tbody tr {
		cursor: pointer;

		&:hover {
			color: #cfd2da;
			text-decoration: none;
			background-color: #434857;
		}
	}
}
</style>

<script>
import * as BlobUtil from 'blob-util'
import Nav from '@/components/Nav.vue'
import SwitchCheckbox from '@/components/SwitchCheckbox.vue'
import PackageIconSelector from '@/components/PackageIconSelector'
import ScreenshotSelector from '@/components/ScreenshotSelector.vue'
import { PackageAPI } from '@/scripts/ApiUtil'
import { required } from 'vuelidate/lib/validators'

let asyncForEach = async (array, callback) => {
	for (let index = 0; index < array.length; index++) {
		await callback(array[index], index)
	}
}

export default {
	name: "packageEdit",
	components: {
		Nav,
		SwitchCheckbox,
		PackageIconSelector,
		ScreenshotSelector
	},
	data() {
		return {
			packageData: null,
			
			selectedPackageIcon: null,
			selectedIconBlob: null,
			screenshots: null,
			deletedScreenshots: [],
			versionData: [],
			
			isDoingWork: false
		}
	},
	validations: {
		packageData: {
			name: { required },
			identifier: { required },
			shortDescription: { required },
			detailedDescription: { required },
			minOSVersion: { required }
		},
		//selectedIcon: { required },
	},
	async created() {
		this.isDoingWork = true;
		let _packageData = await PackageAPI.getPackage(this.$route.params.packageId);
		if (_packageData.status == 200) {
			let _packageKeys = ["id", "name", "identifier", "shortDescription", "detailedDescription", "bugsReportURL", "minOSVersion", "maxOSVersion", "visible", "icon"];

			this.packageData = Object.keys(_packageData.data)
				.filter(key => _packageKeys.includes(key))
				.reduce((obj, key) => {
					obj[key] = _packageData.data[key];
					return obj
				}, {});
			
			this.screenshots = JSON.parse(_packageData.data.screenshots);

			let versionData = await PackageAPI.getPackageVersions(this.$route.params.packageId);
			this.versionData = versionData.data;
			this.isDoingWork = false;
		} else {
			this.packageData = {
				status: _packageData.status
			}
			this.isDoingWork = false;
		}
	},
	mounted() {
		window.scrollTo(0,0);
	},
	filters: {
		date(value) {
			return new Date(value).toLocaleDateString("en-US");
		}
	},
	methods: {
		cancel() {
			const self = this;
			bootbox.confirm({
				title: "Canel?",
				message: "Are you sure you want to cancel? All unsaved progress will be lost.",
				buttons: {
					confirm: {
						label: 'OK',
						className: 'btn-danger'
					},
					cancel: {
						label: 'Cancel',
						className: 'btn-secondary'
					}
				},
				callback: async (result) => {
					if (result) {
						self.$router.replace(`/packages/`);
					}
				}
			});
		},
		async savePackage() {
			this.isDoingWork = true;
			this.packageData.screenshots = JSON.stringify(this.screenshots);
			
			let result = await PackageAPI.updatePackage(this.$route.params.packageId, this.packageData);
			if (result.status == 200) {
				if (this.deletedScreenshots.length) {
					asyncForEach(this.deletedScreenshots, async file => {
						await PackageAPI.deletePackageScreenshot(this.$route.params.packageId, file);
					});
				}
				
				if (this.selectedPackageIcon) {
					let iconUploadResult = await PackageAPI.uploadPackageIcon(this.$route.params.packageId, this.selectedPackageIcon);
					if (iconUploadResult.status != 200) {
						this.isDoingWork = false;
						return bootbox.alert(`There was an error uploading the package icon.<br>You need to manually upload the package icon again through the Package editor.<br><br>${iconUploadResult.data.message}`);
					}
				}
				
				this.$v.$reset();
				this.isDoingWork = false;
			} else {
				this.isDoingWork = false;
				bootbox.alert(`There was an error updating the package.<br><br>${result.data.message}`);
			}
		},
		async deleteVersion(versionId) {
			const self = this;
			bootbox.confirm({
				title: "Delete Version?",
				message: "Are you sure you want to delete this version?<br>This action cannot be undone.",
				buttons: {
					confirm: {
						label: 'Delete',
						className: 'btn-danger'
					},
					cancel: {
						label: 'Cancel',
						className: 'btn-secondary'
					}
				},
				callback: async (result) => {
					if (result) {
						let result = await PackageAPI.deletePackageVersion(this.$route.params.packageId, versionId);
						if (result.status != 200) return bootbox.alert(`There was an error deleting this version.<br><br>${result.data.message}`);
						
						let versionData = await PackageAPI.getPackageVersions(this.$route.params.packageId);
						this.versionData = versionData.data;
					}
				}
			});
		},
		iconChanged(file) {
			this.$v.$touch();
			
			if (file) {
				this.packageData.icon = `${this.packageData.id}.png`
			} else {
				this.packageData.icon = "";
			}
		}
	}
}
</script>