<template>
	<div class="screenshot-container" ngf-drop="fileSelected($files, currentDevice)" :class="{'working': isDoingWork}">
		<div class="screenshot-header">
			<ul class="tab-group">
				<li class="tab-group-item iphone-55" :class="{'current': currentDevice == 'iphone'}" @click="setCurrentDevice('iphone')">
					<a @click.prevent href="">
						<div class="tab-bg"></div>
						<div class="tab-content-container">
							<p class="tab-content"></p>
							<p class="tab-content">iPhone</p>
							<p class="tab-content">4.7" / 5.5" / 5.8"</p>
						</div>
					</a>
				</li>
				<li class="tab-group-item ipad-129" :class="{'current': currentDevice == 'ipad'}" @click="setCurrentDevice('ipad')">
					<a @click.prevent href="">
						<div class="tab-bg"></div>
						<div class="tab-content-container">
							<p class="tab-content"></p>
							<p class="tab-content">iPad</p>
							<p class="tab-content">9.7" / 10.5" / 12.9"</p>
						</div>
					</a>
				</li>
			</ul>
		</div>
		<div class="screenshot-scroll-wrapper" v-on:dragenter="dropIn" v-on:dragover="dropIn" v-on:dragleave="dropOut" v-on:drop="dropFile">
			<span class="drop-text" v-if="!screenshots[currentDevice].length">
				<p>Drag up to 10 screenshots here.</p>
				<!-- <p>These will be used for all iPhone display sizes.</p> -->
			</span>
			<div class="screenshot-scroll-container" v-show="screenshots[currentDevice].length">
				<div class="screenshot" v-for="(screenshot, index) in screenshots[currentDevice]" @click="deleteScreenshot(screenshot, index, currentDevice)">
					<img :src="`/resources/screenshots/${packageData.id}_${screenshot}~${currentDevice}.png`">
				</div>
			</div>
		</div>
		<div class="screenshot-footer">
			<div class="screenshot-action">{{screenshots[currentDevice].length || "0"}} out of 10 Screenshots</div>
			<div class="screenshot-action">
				<label for="screenshot-selector" class="m-b-0" :class="{'disabled': screenshots[currentDevice].length >= 10}">Choose File</label>
					
				<input id="screenshot-selector" multiple type="file" @change="fileSelected($event.target.files, currentDevice)" accept="image/png" style="position: absolute; top: 80%; left: -9999px">
			</div>
		</div>
	</div>
</template>

<script>
import Path from 'path'
import { PackageAPI } from '@/scripts/ApiUtil.js'

let supportedImageSizes = {
			ipad: ["1024x768","768x1024","2048x1536","1536x2048", "2224x1668","1668x2224", "2732x2048","2048x2732"],
			iphone: ["960x640","640x960", "1136x640","640x1136", "1334x750","750x1334", "828x1472", "1472x828", "2208x1242", "1242x2208", "2436x1125", "1125x2436"],
		};

export default {
	name: "ScreenshotSelector",
	props: ["value", "deletedScreenshots", "packageObj"],
	data() {
		return {
			screenshots: this.$props.value,
			deletedFiles: this.$props.deletedScreenshots,
			packageData: this.$props.packageObj,
			currentDevice: "iphone",
			isDoingWork: false
		}
	},
	methods: {
		dropIn(e) {
			e.preventDefault();
			e.stopPropagation();
			
			e.target.classList.add("drop-in");
		},
		dropOut(e) {
			e.preventDefault();
			e.stopPropagation();
			
			e.target.classList.remove("drop-in");
		},
		dropFile(e) {
			e.preventDefault();
			e.stopPropagation();
			
			e.target.classList.remove("drop-in");
			
			let file = e.dataTransfer.files[0];
			if (["image/png"].includes(file.type)) {
				this.fileSelected([file], this.currentDevice);
			}
		},
		
		setCurrentDevice(device) {
			this.currentDevice = device;
		},
		fileSelected(files, device) {
			const self = this;
			
			
			this.isDoingWork = true;
			Array.from(files).forEach((file, index) => {
				let reader = new FileReader();
				reader.onload = function() {
					let img = new Image();
					
					img.onload = async function() {
						if (supportedImageSizes[device].indexOf(`${img.width}x${img.height}`) >= 0) {
							if (!self.screenshots[device]) {
								self.screenshots[device] = [];
							}
							
							let fileName = file.name.split(".").slice(0, -1).join(".");
							let response = await PackageAPI.uploadPackageScreenshot(self.$route.params.packageId, file, `${self.packageData.id}_${fileName}~${device}.png`);
							if (response.status != 200) return bootbox.alert("There was an error uploading one or more screenshots");
							
							self.screenshots[device].push(fileName);
							
							if (index == files.length -1) {
								setTimeout(() => {
									self.isDoingWork = false
								}, 200);
							}
						} else {
							self.isDoingWork = false
						}
					}
					
					img.src = reader.result;
				}
				
				reader.readAsDataURL(file);
			});
			
			this.$emit("touched");
		},
		deleteScreenshot(fileName, index, device) {
			this.deletedFiles.push(`${this.packageData.id}_${fileName}~${device}.png`);
			this.screenshots[device].splice(index, 1);
			
			this.$emit("touched");
		}
	}
}
</script>